// Service API - connexion au backend FastAPI
import axios from 'axios';

// Configurer l'URL de base de l'API
const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

// Crer une instance axios
const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Intercepteur de requte
api.interceptors.request.use(
  (config) => {
    console.log(` API Request: ${config.method?.toUpperCase()} ${config.url}`);
    return config;
  },
  (error) => {
    console.error(' API Request Error:', error);
    return Promise.reject(error);
  }
);

// Intercepteur de rponse
api.interceptors.response.use(
  (response) => {
    console.log(` API Response: ${response.config.url} - ${response.status}`);
    return response;
  },
  (error) => {
    console.error(' API Response Error:', error.response?.data || error.message);
    return Promise.reject(error);
  }
);

// Classe de service API
class ApiService {
  // API lies au systme
  async getSystemData() {
    try {
      // 
      // Rcupration des donnes systme (peut tre lente car calcule les chemins pour chaque colis)
      const response = await api.get('/api/system/data', { timeout: 60000 });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des donnes systme: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getSystemStats() {
    try {
      const response = await api.get('/api/system/stats');
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des statistiques systme: ${error.response?.data?.detail || error.message}`);
    }
  }

  async regenerateSystem() {
    try {
      // Rgnration impliquant la gnration de donnes et l'insertion en masse, augmenter le timeout
      const response = await api.post('/api/system/regenerate', {}, { timeout: 60000 });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rgnration du systme: ${error.response?.data?.detail || error.message}`);
    }
  }

  async healthCheck() {
    try {
      const response = await api.get('/api/health');
      return response.data;
    } catch (error) {
      throw new Error(`chec du contrle de sant: ${error.response?.data?.detail || error.message}`);
    }
  }

  // API lies aux colis
  async getPackages(limit = null, category = null) {
    try {
      const params = {};
      if (limit !== null) params.limit = limit;
      if (category !== null) params.category = category;
      
      const response = await api.get('/api/packages', { params });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration de la liste des colis: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getPackage(packageId) {
    try {
      const response = await api.get(`/api/packages/${packageId}`);
      return response.data;
    } catch (error) {
      if (error.response?.status === 404) {
        // Frontend-side not found error (translated to French)
        throw new Error('Colis introuvable');
      }
      throw new Error(`chec de la rcupration des informations du colis: ${error.response?.data?.detail || error.message}`);
    }
  }

  async searchPackages(query) {
    try {
      const response = await api.post('/api/packages/search', { query });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la recherche de colis: ${error.response?.data?.detail || error.message}`);
    }
  }

  async createScheduledPackage({ src, dst, category = 0, sendTime = 0 }) {
    try {
      const response = await api.post('/api/packages/schedule', {
        src,
        dst,
        category,
        sendTime
      });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la cration du colis: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getPackagesBatch(ids = []) {
    try {
      const response = await api.post('/api/packages/batch', { ids });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des colis en lot: ${error.response?.data?.detail || error.message}`);
    }
  }

  // API lies aux itinraires
  async calculatePath(src, dst, category = 0) {
    try {
      const response = await api.post('/api/path/calculate', {
        src,
        dst,
        category
      });
      return response.data;
    } catch (error) {
      throw new Error(`chec du calcul d'itinraire: ${error.response?.data?.detail || error.message}`);
    }
  }

  async calculateAlternativePath(src, dst, avoidNode, category = 0) {
    try {
      const response = await api.post('/api/path/alternative', {
        src,
        dst,
        avoid_node: avoidNode,
        category
      });
      return response.data;
    } catch (error) {
      throw new Error(`chec du calcul d'itinraire alternatif: ${error.response?.data?.detail || error.message}`);
    }
  }

  // API lies au rseau
  async getNodes() {
    try {
      const response = await api.get('/api/nodes');
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des donnes des nuds: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getNodeCounts() {
    try {
      const response = await api.get('/api/nodes/counts');
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration du nombre de colis par nud: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getNodeCountsAt(timestamp = 0) {
    try {
      const response = await api.get('/api/nodes/counts_at', { params: { timestamp } });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des compteurs de colis par temps: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getPackagesAtNode(nodeId, timestamp = null) {
    try {
      const params = {};
      if (timestamp !== null) params.timestamp = timestamp;
      const response = await api.get(`/api/nodes/${nodeId}/packages_at`, { params });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des colis prsents au nud  l'instant spcifi: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getSimBounds() {
    try {
      const response = await api.get('/api/system/sim_bounds');
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration de la plage de temps de simulation: ${error.response?.data?.detail || error.message}`);
    }
  }

  async getEdges(edgeType = null) {
    try {
      const params = {};
      if (edgeType) params.edge_type = edgeType;
      
      const response = await api.get('/api/edges', { params });
      return response.data;
    } catch (error) {
      throw new Error(`chec de la rcupration des donnes d'artes: ${error.response?.data?.detail || error.message}`);
    }
  }
}

// Crer une instance du service API
const apiService = new ApiService();

// Exporter le service API et fonctions utilitaires
export default apiService;

export const checkBackendConnection = async () => {
  try {
    await apiService.healthCheck();
    return { connected: true, message: 'Connexion au backend OK' };
  } catch (error) {
    return {
      connected: false,
      message: `Connexion au backend choue: ${error.message}`,
      suggestion: 'Assurez-vous que le serveur backend est en cours d excution (python main.py)'
    };
  }
};

export const formatApiError = (error) => {
  if (error.response) {
    // Le serveur a renvoy une rponse d'erreur
    return {
      type: 'server_error',
      message: error.response.data?.detail || 'Erreur serveur',
      status: error.response.status
    };
  } else if (error.request) {
    // Requte envoye mais aucune rponse reue
    return {
      type: 'network_error',
      message: 'chec de la connexion rseau, vrifiez si le backend est dmarr',
      suggestion: "Excutez start_backend.bat pour dmarrer le serveur backend"
    };
  } else {
    // Autres erreurs
    return {
      type: 'unknown_error',
      message: error.message || 'Erreur inconnue'
    };
  }
};